<script>
import { 
  Modal, 
  Title, 
  FormWrapper, 
  FormInput, 
  ButtonWrapper, 
  Button, 
  CloseButton, 
  Header, 
  Footer,
  ErrorModal,
  Icon } from '../style.js';
import styled from 'vue-styled-components';
import Textarea from './ui/textarea.vue';
import TableList from './ui/tableList.vue';
import Loading from './ui/loading.vue';
import data from '../data/profile.json';

const profileTableProps = { isEdited: String }
const ProfileTable = styled('table', profileTableProps)`
  border-collapse: collapse;
  width: 90%;
  margin: 0 auto; 
  table-layout: fixed;
  .nodata {
    color: #05b077;
    display: flex;
    align-items: center;
  }
  p{
    margin: 0;
  } 
  .profile__interest {
    th {
      border-bottom: none;
    }
    td > p {
      margin-bottom: 15px;
    }
    &:last-child td {
      border-bottom: none;
    }
  }
  tr {
    vertical-align: top;
    &.highlight {
      background: rgba(227, 250, 250, 0.5);
    }
  }
  th {
    padding: 10px 0;
    margin-right: 20px;
    border-bottom: 1px solid grey;
    font-size: 18px;
    color: #324c5a;
    font-weight: bold;
    text-align: left;
    width: 80px;
  }
  td {
    padding: 10px;
    border-bottom: 1px solid grey;
    color: #324c5a;
    font-size: 16px;
    max-width: 70%;
    & > p {
      font-size: 14px;
      color: #628ea7;
    }
    &.profile__service {
      width: 140px;
      vertical-align: middle;
      p {
        margin: 0;
      }
    }
    &.profile__form {
      max-width: 70%;
    }
  }
`

const ProfileFormWrapper = styled(FormWrapper)`
  & div {
    margin-bottom: 10px;
  }
`

export default {
  name: 'Profile',
  props: {
    msg: String
  },
  components: {
    Modal,
    Title,
    ProfileTable,
    ButtonWrapper,
    Button,
    CloseButton,
    FormWrapper,
    FormInput,
    Header,
    Footer,
    ProfileFormWrapper,
    Textarea,
    TableList,
    ErrorModal,
    Loading,
    Icon
  },
  methods: {
    getData() {
      this.queryData = data;
      if(this.queryData.ErrMsg.returnCode === 0) {
        this.tableData = this.queryData.perDataMoreMap
        this.groupHobbies();
      } else {
        this.isError = true;
      }
    },
    groupHobbies() {
      const hobbies = {
        "L010001": "",
        "L010002": "",
        "L010003": "",
        "L010004": "",
        "L010005": "",
        "L020001": "",
        "L020002": "",
        "L020003": "",
        "L020004": "",
        "L020005": "",
        "L020006": "",
        "L020007": "",
        "L020008": "",
        "L030001": "",
        "L030002": "",
        "L030003": "",
        "L030004": "",
        "L030005": "",
        "L030006": "",
        "L040001": "",
        "L040002": "3C",
        "L040003": "",
        "L050001": "",
        "L050002": "/",
        "L050003": "",
        "L050004": "",
        "L050005": "",
        "L050006": "",
        "L050007": "",
        "L050008": ""
      }
      const category = {
        "L01": "",
        "L02": "",
        "L03": "",
        "L04": "",
        "L05": "",
      }
      this.interestsData = this.tableData.hobbiesList.reduce((acc, hobby) => {
        let key = category[hobby.slice(0,3)];
        (acc[key] = acc[key] || []).push(hobbies[hobby]);
        return acc;
      }, {});
    },
    sendForm() {
      this.toggleEdit();
    },
    toggleEdit(field = null) {
      if(field) {
        this.highlightField = field;
      } else {
        this.highlightField = '';
      }
      this.isEdit = !this.isEdit;
    },
    refreshPage() {
      this.$router.go({
        path: '/',
        force: true
      })
    },
    salaryTrans(salaryStr) {
      const salaryNum = Number(salaryStr);
      if(salaryNum < 310000) return '30';
      else if(salaryNum >= 310000 && salaryNum < 610000) return '31-60';
      else if(salaryNum >= 610000 && salaryNum < 1010000) return '61-100';
      else if(salaryNum >= 1010000 && salaryNum < 2010000) return '101-200';
      else return '201 '; 
    },
    dateParser(timeStr) {
      if(timeStr) return `${timeStr.split(' ')[0]} ${timeStr.split(' ')[1].split(':').slice(0, 2).join(':')}`;
      else timeStr; 
    }
  },
  data () {
    return {
      isLoading: false,
      isError: false,
      isEdit: false,
      highlightField: '',
      tableData: null,
      queryData: null,
      interestsData: null,
      educationStatus:{
        "1": "",
        "2": "",
        "3": "",
        "4": ""
      },
      marrigeStatus: {
        "0": "",
        "1": "",
        "2": ""
      },
      featuresStauts: {
        "S030001": "",
        "S030002": "",
        "S030003": "",
        "S030004": "",
        "S030005": "",
      },
      spcStatus: {
        "B99001Map": "",
        "B99010Map": "",
        "B99013Map": "", 
        "B99004Map": "",
        "B99009Map": "",
        "B99005Map": ""
      },
      form: {
        specialReason: '',
        featureStr: [],
        tagStr: '',
        marrige: '',
        child: 0,
        salary: '',
        education: '',
        hobbies: []
      },
      userFields: {
        'specialReason': '',
        'featureStr': ['','', '', '', ''],
        'tagStr': ['', ''],
        'marrige': ['', ''],
        'salary': ['30', '31-60', '61-100', '101-200', '201 '],
        'education': ['', '', '/', '']
      },
      interestFields: {
        '': ['','', '', '', '', '', '', '', ''],
        '': ['', '', '', '//', '', '', '', ''],
        '': ['', '', '', '', '', ''],
        '': ['', '', '', '', '3C'],
        '': ['', '', '', '', '', '', '']
      }
    }
  },
  mounted: function(){
    this.getData();
    this.isEdit = this.$route.query.edit;
  }
}
</script>
